body {
  display: flex;
  justify-content: center;
  align-items: center;
}

#quote-box {
  border: 1px solid;
  border-radius: 3px;
  width: 600px;
  position: relative;
  padding: 50px;
  margin-top: 100px;
  display: table;
}

.quote-text {
  width: 550px;
  text-align: center;
}

.quote-author {
  text-align: right;
}

.buttons {
  display: block;
}

#new-quote {
  float: right;
}

#new-quote:hover {
  opacity: 0.8;
}

#tweet-quote {
  float: left;
}

